"use client";

import React, { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import '../styles/sidebar.css';

interface SidebarProps {
    onToggle?: (isOpen: boolean) => void;
}

const Sidebar: React.FC<SidebarProps> = ({ onToggle }) => {
    const [isOpen, setIsOpen] = useState(true);

    const toggleSidebar = () => {
        const newState = !isOpen;
        setIsOpen(newState);
        onToggle?.(newState);
    };

    return (
        <>
            {isOpen && <div className="sidebar-overlay" onClick={toggleSidebar} />}
            <aside className={`sidebar ${isOpen ? 'open' : 'closed'}`}>
                <div className="sidebar-header">
                    <Image 
                        src="/images/iconeescuro.png" 
                        alt="CineVault Icon" 
                        width={40} 
                        height={40}
                        className="sidebar-logo"
                    />
                    {isOpen && <h1 className="sidebar-title">CineVault</h1>}
                </div>
                
                <nav className="sidebar-nav">
                    <Link href="/" className="nav-link">
                        <span className="nav-icon">üè†</span>
                        {isOpen && <span className="nav-text">Home</span>}
                    </Link>
                    <Link href="/filmes" className="nav-link">
                        <span className="nav-icon">üé¨</span>
                        {isOpen && <span className="nav-text">Filmes</span>}
                    </Link>
                    <Link href="/series" className="nav-link">
                        <span className="nav-icon">üì∫</span>
                        {isOpen && <span className="nav-text">S√©ries</span>}
                    </Link>
                    <Link href="/animes" className="nav-link">
                        <span className="nav-icon">üé®</span>
                        {isOpen && <span className="nav-text">Animes</span>}
                    </Link>
                </nav>

                <button className="sidebar-toggle" onClick={toggleSidebar} title="Toggle menu">
                    {isOpen ? '‚Üê' : '‚Üí'}
                </button>
            </aside>
        </>
    );
};

export default Sidebar;